import { PublicClientApplication } from '@azure/msal-browser';
import { Client } from '@microsoft/microsoft-graph-client';

class PlannerAddin {
    constructor() {
        this.msalInstance = null;
        this.graphClient = null;
        this.currentEmail = null;
        this.plans = [];
        this.buckets = [];
        
        this.initializeMsal();
        this.bindEvents();
    }

    initializeMsal() {
        const msalConfig = {
            auth: {
                clientId: '79d95835-d4b5-4a6c-a313-668e4897aef9', // Replace with your app ID
                authority: 'https://login.microsoftonline.com/common',
                redirectUri: window.location.origin
            },
            cache: {
                cacheLocation: 'localStorage',
                storeAuthStateInCookie: true
            }
        };

        this.msalInstance = new PublicClientApplication(msalConfig);
    }

    bindEvents() {
        document.getElementById('login-btn').addEventListener('click', () => this.login());
        document.getElementById('send-btn').addEventListener('click', () => this.sendToPlanner());
        document.getElementById('refresh-btn').addEventListener('click', () => this.loadPlannerBoards());
        document.getElementById('planner-select').addEventListener('change', (e) => this.onPlannerChange(e));
    }

    async login() {
        try {
            const loginRequest = {
                scopes: [
                    'User.Read',
                    'Group.Read.All',
                    'Group.ReadWrite.All',
                    'Tasks.ReadWrite',
                    'Mail.Read',
                    'Mail.Send'
                ]
            };

            const response = await this.msalInstance.loginPopup(loginRequest);
            
            if (response) {
                this.initializeGraphClient(response.accessToken);
                document.getElementById('auth-section').style.display = 'none';
                document.getElementById('main-content').style.display = 'block';
                
                await this.loadCurrentEmail();
                await this.loadPlannerBoards();
                
                this.showStatus('Successfully signed in!', 'success');
            }
        } catch (error) {
            console.error('Login failed:', error);
            this.showStatus('Login failed: ' + error.message, 'error');
        }
    }

    initializeGraphClient(accessToken) {
        this.graphClient = Client.init({
            authProvider: (done) => {
                done(null, accessToken);
            }
        });
    }

    async loadCurrentEmail() {
        try {
            Office.context.mailbox.item.subject.getAsync((result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    document.getElementById('email-subject').textContent = result.value;
                    document.getElementById('task-title').value = result.value;
                }
            });

            Office.context.mailbox.item.sender.getAsync((result) => {
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    document.getElementById('email-sender').textContent = result.value.emailAddress;
                }
            });

            Office.context.mailbox.item.body.getAsync(
                { coercionType: Office.CoercionType.Text },
                (result) => {
                    if (result.status === Office.AsyncResultStatus.Succeeded) {
                        this.currentEmail = {
                            subject: result.value, // This will be set properly below
                            body: result.value,
                            receivedTime: Office.context.mailbox.item.dateTimeCreated,
                            itemId: Office.context.mailbox.item.itemId
                        };
                        
                        // Get subject and sender properly
                        Office.context.mailbox.item.subject.getAsync((subjectResult) => {
                            if (subjectResult.status === Office.AsyncResultStatus.Succeeded) {
                                this.currentEmail.subject = subjectResult.value;
                            }
                        });
                        
                        Office.context.mailbox.item.sender.getAsync((senderResult) => {
                            if (senderResult.status === Office.AsyncResultStatus.Succeeded) {
                                this.currentEmail.sender = senderResult.value;
                            }
                        });
                    }
                }
            );

        } catch (error) {
            console.error('Error loading email:', error);
            this.showStatus('Error loading email details', 'error');
        }
    }

    async loadPlannerBoards() {
        try {
            document.getElementById('planner-select').innerHTML = '<option value="">Loading boards...</option>';
            document.getElementById('send-btn').disabled = true;
            
            // First try to get Teams
            let groups = [];
            try {
                const teamsResponse = await this.graphClient.api('/me/joinedTeams').get();
                groups = teamsResponse.value || [];
            } catch (teamsError) {
                console.warn('Could not load Teams, trying Groups instead:', teamsError);
                try {
                    const groupsResponse = await this.graphClient.api('/me/memberOf').select('id,displayName').get();
                    groups = groupsResponse.value?.filter(g => g['@odata.type'] === '#microsoft.graph.group') || [];
                } catch (groupsError) {
                    throw new Error('Cannot access Teams or Groups. Please ensure you have the necessary permissions.');
                }
            }

            if (groups.length === 0) {
                throw new Error('No Teams or Groups found. Please join a Team with Planner boards first.');
            }
            
            this.plans = [];
            let loadedPlansCount = 0;
            
            for (const group of groups) {
                try {
                    const plans = await this.graphClient.api(`/groups/${group.id}/planner/plans`).get();
                    if (plans.value && plans.value.length > 0) {
                        this.plans.push(...plans.value.map(plan => ({
                            ...plan,
                            groupId: group.id,
                            groupName: group.displayName
                        })));
                        loadedPlansCount++;
                    }
                } catch (planError) {
                    // Silently skip groups without Planner access
                    console.debug(`No Planner access for group ${group.displayName}`);
                }
            }

            const plannerSelect = document.getElementById('planner-select');
            
            if (this.plans.length === 0) {
                plannerSelect.innerHTML = '<option value="">No Planner boards found</option>';
                throw new Error(`No Planner boards found. Checked ${groups.length} Teams/Groups.`);
            }
            
            plannerSelect.innerHTML = '<option value="">Select a Planner board...</option>';
            this.plans.forEach(plan => {
                const option = document.createElement('option');
                option.value = plan.id;
                option.textContent = `${plan.title} (${plan.groupName})`;
                plannerSelect.appendChild(option);
            });

            document.getElementById('send-btn').disabled = false;
            this.showStatus(`Found ${this.plans.length} Planner board(s) across ${loadedPlansCount} Teams`, 'success');

        } catch (error) {
            console.error('Error loading Planner boards:', error);
            document.getElementById('planner-select').innerHTML = '<option value="">Error loading boards</option>';
            
            let userMessage = 'Error loading Planner boards: ';
            if (error.message.includes('permissions')) {
                userMessage += 'Missing permissions. Please contact your administrator.';
            } else if (error.message.includes('No Teams')) {
                userMessage += 'No Teams found. Please join a Team with Planner first.';
            } else if (error.message.includes('No Planner boards')) {
                userMessage += 'No Planner boards found. Please create a Planner board in your Teams first.';
            } else {
                userMessage += error.message;
            }
            
            this.showStatus(userMessage, 'error');
        }
    }

    async onPlannerChange(event) {
        const planId = event.target.value;
        const bucketSelect = document.getElementById('bucket-select');
        
        if (!planId) {
            bucketSelect.innerHTML = '<option value="">Select a board first</option>';
            bucketSelect.disabled = true;
            return;
        }

        try {
            bucketSelect.innerHTML = '<option value="">Loading buckets...</option>';
            bucketSelect.disabled = false;

            const buckets = await this.graphClient.api(`/planner/plans/${planId}/buckets`).get();
            this.buckets = buckets.value;

            bucketSelect.innerHTML = '<option value="">Select a bucket...</option>';
            this.buckets.forEach(bucket => {
                const option = document.createElement('option');
                option.value = bucket.id;
                option.textContent = bucket.name;
                bucketSelect.appendChild(option);
            });

        } catch (error) {
            console.error('Error loading buckets:', error);
            this.showStatus('Error loading buckets: ' + error.message, 'error');
            bucketSelect.innerHTML = '<option value="">Error loading buckets</option>';
        }
    }

    async sendToPlanner() {
        const sendBtn = document.getElementById('send-btn');
        
        try {
            const planId = document.getElementById('planner-select').value;
            const bucketId = document.getElementById('bucket-select').value;
            const taskTitle = document.getElementById('task-title').value.trim();
            const taskDescription = document.getElementById('task-description').value.trim();

            // Validation
            if (!planId) {
                this.showStatus('Please select a Planner board', 'error');
                return;
            }
            
            if (!bucketId) {
                this.showStatus('Please select a bucket', 'error');
                return;
            }
            
            if (!taskTitle) {
                this.showStatus('Please enter a task title', 'error');
                return;
            }

            if (taskTitle.length > 255) {
                this.showStatus('Task title is too long (max 255 characters)', 'error');
                return;
            }

            sendBtn.disabled = true;
            this.showStatus('Creating task in Planner...', 'success');

            // Validate current email exists
            if (!this.currentEmail) {
                throw new Error('Email content not loaded. Please refresh and try again.');
            }

            // Get email content as EML with timeout
            let emlContent;
            try {
                emlContent = await Promise.race([
                    this.getEmailAsEml(),
                    new Promise((_, reject) => 
                        setTimeout(() => reject(new Error('Timeout getting email content')), 10000)
                    )
                ]);
            } catch (emlError) {
                console.warn('Could not get email content:', emlError);
                emlContent = 'Email content could not be retrieved';
            }
            
            // Create the task with retry logic
            let task;
            const maxRetries = 3;
            for (let attempt = 1; attempt <= maxRetries; attempt++) {
                try {
                    const taskData = {
                        planId: planId,
                        bucketId: bucketId,
                        title: taskTitle,
                        assignments: {}
                    };

                    task = await this.graphClient.api('/planner/tasks').post(taskData);
                    break; // Success, exit retry loop
                    
                } catch (taskError) {
                    if (attempt === maxRetries) {
                        throw taskError;
                    }
                    console.warn(`Task creation attempt ${attempt} failed:`, taskError);
                    this.showStatus(`Retrying... (${attempt}/${maxRetries})`, 'success');
                    await new Promise(resolve => setTimeout(resolve, 1000)); // Wait 1 second
                }
            }

            if (!task || !task.id) {
                throw new Error('Task was created but no ID returned');
            }

            this.showStatus('Task created, adding details...', 'success');

            // Update task details with retry logic
            try {
                const taskDetails = {
                    description: this.buildTaskDescription(taskDescription, emlContent),
                    references: {
                        'originalEmail': {
                            alias: 'Original Email',
                            type: 'other'
                        }
                    }
                };

                await this.graphClient.api(`/planner/tasks/${task.id}/details`).patch(taskDetails);
                
            } catch (detailsError) {
                console.warn('Could not update task details:', detailsError);
                // Don't fail the entire operation for details update
            }

            // Store email metadata for comment sync
            try {
                await this.storeEmailMetadata(task.id, this.currentEmail.sender, this.currentEmail.itemId);
                this.setupCommentMonitoring(task.id);
            } catch (metadataError) {
                console.warn('Could not set up comment monitoring:', metadataError);
                // Don't fail for monitoring setup
            }

            this.showStatus(` Email successfully sent to Planner! Task: "${task.title}"`, 'success');
            
            // Reset form
            document.getElementById('task-title').value = '';
            document.getElementById('task-description').value = '';

        } catch (error) {
            console.error('Error sending to Planner:', error);
            
            let userMessage = 'Failed to send email to Planner: ';
            if (error.code === 'Forbidden' || error.status === 403) {
                userMessage += 'You do not have permission to create tasks in this Planner board.';
            } else if (error.code === 'NotFound' || error.status === 404) {
                userMessage += 'The selected Planner board or bucket was not found.';
            } else if (error.message.includes('Timeout')) {
                userMessage += 'Operation timed out. Please try again.';
            } else if (error.message.includes('network') || error.message.includes('offline')) {
                userMessage += 'Network error. Please check your connection and try again.';
            } else {
                userMessage += error.message || 'Unknown error occurred.';
            }
            
            this.showStatus(userMessage, 'error');
            
        } finally {
            sendBtn.disabled = false;
        }
    }

    async getEmailAsEml() {
        return new Promise((resolve) => {
            Office.context.mailbox.item.body.getAsync(
                { coercionType: Office.CoercionType.Html },
                (result) => {
                    if (result.status === Office.AsyncResultStatus.Succeeded) {
                        const emlContent = this.convertToEml(result.value);
                        resolve(emlContent);
                    } else {
                        resolve('Email content could not be retrieved');
                    }
                }
            );
        });
    }

    convertToEml(htmlBody) {
        const subject = Office.context.mailbox.item.subject;
        const sender = Office.context.mailbox.item.sender;
        const dateTime = Office.context.mailbox.item.dateTimeCreated;
        
        return `Subject: ${subject}
From: ${sender?.emailAddress || 'Unknown'}
Date: ${dateTime?.toISOString() || new Date().toISOString()}
Content-Type: text/html; charset=utf-8

${htmlBody}`;
    }

    buildTaskDescription(additionalNotes, emlContent) {
        let description = ` **Email imported from Outlook**\n\n`;
        
        if (additionalNotes) {
            description += `**Additional Notes:**\n${additionalNotes}\n\n`;
        }
        
        description += `**Original Email Content:**\n\`\`\`\n${emlContent}\n\`\`\`\n\n`;
        description += `*This task was created from an email. Comments added here will be sent back to the original sender.*`;
        
        return description;
    }

    async storeEmailMetadata(taskId, sender, itemId) {
        const metadata = {
            taskId,
            originalSender: sender?.emailAddress,
            originalItemId: itemId,
            createdAt: new Date().toISOString(),
            syncEnabled: true
        };
        
        localStorage.setItem(`planner_task_${taskId}`, JSON.stringify(metadata));
    }

    setupCommentMonitoring(taskId) {
        console.log(`Setting up comment monitoring for task ${taskId}`);
        console.warn('Note: Real-time comment monitoring requires webhook setup or periodic manual checking.');
        console.warn('Microsoft Graph API does not directly expose Planner task comments.');
        console.warn('Consider implementing via Microsoft Graph webhooks or description field changes.');
        
        // This is a simplified version - in production you would need:
        // 1. Microsoft Graph webhooks for real-time notifications
        // 2. Monitor task description field changes (where comments might be appended)
        // 3. Use Microsoft Teams integration if tasks are in Teams-connected plans
        
        const checkTaskUpdates = async () => {
            try {
                const metadata = JSON.parse(localStorage.getItem(`planner_task_${taskId}`));
                if (!metadata || !metadata.syncEnabled) return;

                // Monitor task details for changes (description updates, progress, etc.)
                const taskDetails = await this.graphClient.api(`/planner/tasks/${taskId}/details`).get();
                const task = await this.graphClient.api(`/planner/tasks/${taskId}`).get();
                
                const lastCheck = new Date(metadata.lastUpdateCheck || metadata.createdAt);
                const taskModified = new Date(task.modifiedDateTime);
                const detailsModified = new Date(taskDetails.modifiedDateTime || task.modifiedDateTime);
                
                if (taskModified > lastCheck || detailsModified > lastCheck) {
                    // Task or details were updated - notify original sender
                    await this.sendTaskUpdateToOriginalSender(metadata, task, taskDetails);
                    metadata.lastUpdateCheck = new Date().toISOString();
                    localStorage.setItem(`planner_task_${taskId}`, JSON.stringify(metadata));
                }
            } catch (error) {
                console.error('Error checking task updates:', error);
            }
        };

        // Check every 5 minutes instead of 30 seconds to be more reasonable
        setInterval(checkTaskUpdates, 300000);
    }

    async sendTaskUpdateToOriginalSender(metadata, task, taskDetails) {
        try {
            if (!metadata.originalSender) return;

            const emailBody = this.buildTaskUpdateEmail(task, taskDetails);
            
            const message = {
                subject: `Task Update: ${task.title}`,
                body: {
                    contentType: 'HTML',
                    content: emailBody
                },
                toRecipients: [{
                    emailAddress: {
                        address: metadata.originalSender
                    }
                }]
            };

            await this.graphClient.api('/me/sendMail').post({ message });
            console.log(`Sent task update to ${metadata.originalSender}`);

        } catch (error) {
            console.error('Error sending task update:', error);
        }
    }

    buildTaskUpdateEmail(task, taskDetails) {
        let body = `<html><body>
        <h2> Task Update: ${task.title}</h2>
        <p>Your email has been updated in Microsoft Planner:</p>
        <div style="background-color: #f8f9fa; border-left: 4px solid #0078d4; padding: 15px; margin: 10px 0;">`;
        
        body += `<p><strong>Task:</strong> ${task.title}</p>`;
        body += `<p><strong>Status:</strong> ${task.percentComplete}% Complete</p>`;
        body += `<p><strong>Priority:</strong> ${task.priority || 'Normal'}</p>`;
        
        if (task.assignees && Object.keys(task.assignees).length > 0) {
            body += `<p><strong>Assigned to:</strong> ${Object.keys(task.assignees).length} team member(s)</p>`;
        }
        
        if (taskDetails.description && taskDetails.description !== this.lastSentDescription) {
            body += `<p><strong>Description Updated:</strong></p>`;
            body += `<div style="margin: 10px 0; padding: 10px; background: white; border-radius: 4px;">${taskDetails.description}</div>`;
        }
        
        body += `<p><small>Last Modified: ${new Date(task.modifiedDateTime).toLocaleString()}</small></p>`;
        
        body += `</div>
        <p><small>This is an automated message from your Outlook Planner integration.</small></p>
        <p><small>To stop receiving these updates, please contact your system administrator.</small></p>
        </body></html>`;
        
        return body;
    }

    showStatus(message, type) {
        const statusDiv = document.getElementById('status');
        statusDiv.textContent = message;
        statusDiv.className = `status ${type}`;
        statusDiv.style.display = 'block';
        
        setTimeout(() => {
            statusDiv.style.display = 'none';
        }, 5000);
    }
}

// Initialize when Office is ready
Office.onReady((info) => {
    if (info.host === Office.HostType.Outlook) {
        new PlannerAddin();
    }
});